      }
    ],
    sortList: [],
    addressSel: ['', ' ', ' ', ' ', ' ', ' ', ' ', '  ', '', '', '', '', '', '', '', '','','','','','',''],
    addressSelChoice:'',
    // 
    address1: [' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' ', ' '],
    // 
